button[data-start] {
  border-radius: 10px;
  padding: 16px;
  font-size: 28px;
  position: absolute;
  top: 50%;
  left: 45%;
  transform: translate(-50%, -50%);
}

button[data-stop] {
  border-radius: 10px;
  padding: 16px;
  font-size: 28px;
  position: absolute;
  top: 50%;
  left: 55%;
  transform: translate(-50%, -50%);
}

